* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

body {
  min-height: 100vh;
  font-family: "Sora", sans-serif;
  user-select: none;
  position: relative;
}

.none {
  display: none;
}

.selected {
  text-decoration: underline;
  text-underline-offset: 10px;
  text-decoration-color: rgb(19, 19, 107);
  text-decoration-thickness: 3px;
