EkBQG;IAEQ,mBAAA;EpBwvBV;AACF","sourcesContent":["$white: white;\r\n$blue: rgb(19, 19, 107);\r\n/* $blue-transparent: rgba(19, 19, 107, 0.699); */\r\n$blue-transparent: rgba(19, 19, 107, 0.493);\r\n$blue-disabled: rgba(19, 19, 107, 0.473);\r\n$gray: rgba(233, 230, 230, 0.425);\r\n$light-gray: lightgray;\r\n$black: rgb(34, 33, 33);\r\n$red: red;\r\n$yellow: rgb(255, 217, 0);\r\n/* $yellow-transparent: rgba(255, 230, 0, 0.63); */\r\n$yellow-transparent: rgba(255, 230, 0, 0.459);\r\n$yellow-disabled: rgba(255, 230, 0, 0.5);\r\n$grey: grey;\r\n$keyboard-key-border: rgb(245, 243, 243);\r\n$keyboard-key-boxShadow: rgb(228, 226, 226);\r\n$closing-cross-border: rgb(245, 245, 245);\r\n$closing-cross-boxShasow: rgb(247, 246, 246);\r\n$overlay:rgba(133, 133, 167, 0.26);\r\n/* $overlay:rgba(57, 57, 114, 0.438); */\r\n$btn-disabled: rgba(19, 19, 107, 0.562);\r\n$darkBlue: rgb(8, 8, 58);","@import './colors';\r\n\r\n// Font\r\n@import url('https://fonts.googleapis.com/css2?family=Sora:wght@400;500;600;700&display=swap');\r\n\r\n// Resets\r\n* {\r\n    box-sizing: border-box;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\nhtml {\r\n    font-size: 100%;\r\n}\r\n\r\nbody {\r\n    min-height: 100vh;\r\n    font-family: 'Sora', sans-serif;\r\n    user-select: none;\r\n    position: relative;\r\n}\r\n\r\n.none {\r\n    display: none;\r\n}\r\n\r\n//typography\r\n.selected {\r\n    text-decoration: underline;\r\n    text-underline-offset: 10px;\r\n    text-decoration-color: $blue;\r\n    text-decoration-thickness: 3px;\r\n    font-weight: 600;\r\n}\r\n\r\na {\r\n    cursor: pointer;\r\n}\r\n\r\nli {\r\n    list-style: none;\r\n}\r\n\r\n// Animations\r\n@keyframes fade-slide-in {\r\n    from {\r\n        transform: translateY(-5rem);\r\n    }\r\n    to {\r\n        transform: translateY(0);\r\n    }\r\n    }\r\n","/* $blue-transparent: rgba(19, 19, 107, 0.699); */\n/* $yellow-transparent: rgba(255, 230, 0, 0.63); */\n/* $overlay:rgba(57, 57, 114, 0.438); */\n@import url(\"https://fonts.